<template>
	<div class="receipts-type-navs">
		<div class="receipts-type-box">
			<div v-for="(item,index) in receiptsArr" :key="index" @click="itemClick(index)" :class="{receiptsactive:currentIndex==index}">{{item}}</div>
		</div>
	</div>
</template>

<script>



export default {
  name: 'ReceiptsTypeNavs',
  data(){
	return{
		receiptsArr:[
			'全部单据',
			'欠款单据',
			'收款单据',
		],
		currentIndex:0
	}
  },
  methods:{
	itemClick(index){
		if(this.currentIndex==index)return
		this.currentIndex=index
		this.$emit("receiptsNavsClick",this.currentIndex)
	}
  }
}
</script >

